import React, { Component } from 'react'
import { DashboardCard } from '../cards'

class DashboardLayout extends Component {
  state = {
    statistic: null,
    year: null,
    flightB: false,
    routeB: false,
    airlineB: false,
    aircraftB: false,
    seatB: false,
    frequencyB: false,
    paxB: false,
    loadfactorB: false
  }

  handleDropdown = (e, data) => {
    console.log(data.value)
    this.setState({ [data.name]: data.value })
  }

  getAirport = year => {
    console.log('Get Airport!')
    if (year === '2017') {
      fetch('http://localhost:4000/Y2017')
        .then(res => res.json())
        .then(data => {
          console.log(data)
          // this.setState({ Airline: data.data, totalflight: data.data.length })
          // this.forceUpdate()
        })
    }
  }

  handleSelectOption = (e, data) => {
    console.log('clicked')
    this.setState({ [data.name]: ![data.name] })
    this.forceUpdate()
  }

  searchResult = () => {
    this.getAirport(this.state.year)
  }

  render() {
    console.log
    return (
      <React.Fragment>
        <DashboardCard
          handleDropdown={this.handleDropdown}
          searchResult={this.searchResult}
          handleSelectOption={this.handleSelectOption}
        />
      </React.Fragment>
    )
  }
}

export { DashboardLayout }
